import Scrollpage from "../components/Scrollpage"
import page from "../data/scrollPage.js";
import "../styles/comusPage.css";
import gsap from "gsap";
import { useGSAP } from "@gsap/react";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { useRef } from "react";
gsap.registerPlugin(useGSAP, ScrollTrigger);
const ComusPage = () => {
  const containerRef = useRef();
  const listRef = useRef();
  //gsap    ,   
  useGSAP(()=>{
    const mm = gsap.matchMedia();
    mm.add("(min-width:769px)",()=>{
        // 
    const scrollW = listRef.current.scrollWidth;
    // View 
    const clientW = window.innerWidth;
    const move = -(scrollW-clientW);
    gsap.to(listRef.current,{
      x: move,
      ease: 'none',
      scrollTrigger: {
        trigger: containerRef.current,
        start: "center center",
        end: `+=${move*(-1)}`,
        scrub: 1,
        pin: true,
        //  pin    :  
        pinSpacing: true,
        markers: false
      }
    });
    });
  },{scope:containerRef});

  // useGSAP(()=>{
  //   // 
  //   const scrollW = listRef.current.scrollWidth;
  //   // View 
  //   const clientW = window.innerWidth;
  //   const move = -(scrollW-clientW);
  //   gsap.to('.page-list',{
  //     x: move,
  //     ease: 'none',
  //     scrollTrigger: {
  //       trigger: containerRef.current,
  //       start: "center center",
  //       end: `+=${move*(-1)}`,
  //       scrub: 1,
  //       pin: true,
  //       //  pin    :  
  //       pinSpacing: true,
  //       markers: false
  //     }
  //   });
  // },{scope:containerRef});
  return (
    <section id="comus-page">
      <div className="page-view" ref={containerRef}>
        <div className="page-list" ref={listRef}>
          {
            page.map((item)=>{
              return (
              <Scrollpage 
                key={item.id}
                id = {item.id}
                title={item.title} 
                desc={item.desc}
                imgName={item.imgName}
              />
              )
            })
          }
        </div>
      </div>
    </section>
  )
};
export default ComusPage