import { Outlet } from "react-router-dom";

const About = () => {
  const menus = ['','','',''];
  return (
    <div id="about">
      <h2></h2>
      <ul>
        {
          menus.map((item)=>{
            return (
              <li>{item}</li>
            )
          })
        }
      </ul>
      <Outlet />
    </div>
  )
}

export default About