import "../styles/HozVideo.css";
import videoData from "../assets/vido/main_slate.mp4";
import gsap from "gsap";
import { useGSAP } from "@gsap/react";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { useRef } from "react";
// 
gsap.registerPlugin(useGSAP,ScrollTrigger);

const HozVideo = () => {
    const containerRef = useRef(null);
    const videoRef = useRef(null);

    //scrollTrigger
    useGSAP(()=>{
        //1.   
        const tl = gsap.timeline();
        //2.  
        tl.to(videoRef.current,{
            height:'100vh'
        })
        .to(videoRef.current,{
            width: '80%',
            height: '80vh',
            borderRadius: '4rem'
        })
        .to('h2',{
            opacity: 0
        }, "<")
        .to('.video-txt',{
            opacity: 0.7
        });

        //scrolltrigger 
        ScrollTrigger.create({
            //tl  !!!   
            animation:  tl,
            trigger:containerRef.current,
            start: "center center",
            end: '+=3000',
            scrub: 1,
            pin:true,
            markers: false
        });
       
    },{scope:containerRef});

  return (
    <section id="hoz-video" ref={containerRef}>
        <h2 ref={videoRef}>  </h2>
        <div className="video-wrap">
            <video loop autoPlay muted ref={videoRef}>
                <source src={videoData} type="video/mp4"/>
            </video>
            <ul className="video-txt">
                <li>
                <span>Con</span> + <span>nect</span>  <span>Wave</span>
                </li>
                <li>
                    <span></span>
                    <span></span>
                    <span>    </span>
                </li>
            </ul>
        </div>
        <h2>   .</h2>
    </section>
  );
}

export default HozVideo