import { useState } from "react";
import "../styles/Footer.css";
const Footer = () => {
  const [isShow, setIsShow] = useState(false);
  const handleClick = ()=>{
    setIsShow(!isShow);
  }
  return (
    <footer>
      <div className="left">
        <h3>   298,17(, 6)</h3>
        <p>Copyright  CONNECTWAVE CO.LTD. All Rights Reserved.</p>
      </div>
      <div className="right">
        {
          isShow &&
        (<ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>)
        }
        <p onClick={handleClick}>
          <span>{isShow ? "" : "" }</span>
          </p>
      </div>
    </footer>
  )
}

export default Footer